/* LOADER */
.load-wrapper {
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
}

span.load {
	display: flex;
	margin-top: 200px;
	margin: 0 auto;
	position: relative;

	animation: rotateLoad infinite linear 1s;
}
span.load.mini {
	width: 15px;
	height: 15px;
}
span.load.little {
	width: 25px;
	height: 25px;
}
span.load {
	width: 60px;
	height: 60px;
}
span.load:before,
span.load:after {
	display: flex;

	position: absolute;

	content: "";
	background: #fff;
	border-radius: 50%;
	width: 55%;
	height: 55%;
}
span.load:before {
	right: 0;
	bottom: 0;
	animation: scaleChange_1 infinite linear 1s;
}
span.load:after {
	top: 0;
	left: 0;
	animation: scaleChange_2 infinite linear 1s;
}
span.load.dark:before,
span.load.dark:after {
	background: #222;
}
span.load.mini:after {
	border-width: 2px;
}
span.load.little:after {
	border-width: 3px;
}
span.load:after {
	border-width: 8px;
}

@keyframes rotateLoad {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}
@keyframes scaleChange_1 {
	0% {
		transform: scale(1);
	}
	50% {
		transform: scale(0);
	}
	100% {
		transform: scale(1);
	}
}
@keyframes scaleChange_2 {
	0% {
		transform: scale(0);
	}
	50% {
		transform: scale(1);
	}
	100% {
		transform: scale(0);
	}
}
