.body-sidebar {
	flex: 0 0 25%;
	display: block;
	z-index: 20;
	height: 100%;
	background: rgb(42, 53, 61);
	padding: 20px;
	font-size: 34px;
	overflow: hidden;
	position: relative;
	&__list {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
	&__list-item {
		font-size: 24px;
		font-weight: 900;
		color: #035a5f;
		width: 80%;
		height: 50px;
		border-radius: 10px;
		background: rgb(10, 52, 114);
		margin-bottom: 20px;
		z-index: 5;
		transition: 0.3s;
		animation-delay: 100ms;
		position: relative;
		&::before {
			content: "";
			z-index: -1;
			position: absolute;
			font-size: 18px;
			font-weight: 900;
			color: rgb(26, 109, 134);
			left: 0;
			bottom: 5px;
			width: 100%;
			height: 50px;
			padding: 0 20px 0 0;
			border-radius: 10px;
			background: rgb(7, 36, 78);
			box-shadow: inset -2px -2px rgb(5, 26, 58);
		}
	}
	&__list-item:hover {
		color: #03e9f4;
	}
	&__list-item:active {
		bottom: 0px;
		background: rgb(7, 36, 78);
		box-shadow: inset 0 0 #555;
		color: #cfe3e4;
		box-shadow: 0px 0px 5px #03e9f4, 0px 0px 7px #03e9f4, 0px 0px 10px #03e9f4, 0px 0px 15px #03e9f4;
		top: 5px;
	}
	&__news-box {
		color: rgb(212, 204, 204);
		width: 100%;
		height: 300px;
		background: rgb(4, 12, 36);
		margin-top: 100px;
		border-radius: 10px;
		&._title {
			height: 20%;
			background: #000;
		}
		&._body {
			background: #000;
			height: 100px;
			width: 100px;
		}
	}
	&__news-box_title {
		border-radius: 10px;
		height: 20%;
		font-size: 24px;
		font-weight: 900;
		letter-spacing: 2px;
		background: rgb(11, 4, 80);
		display: flex;
		justify-content: center;
		align-items: center;
	}
	&__news-box_body {
		font-size: 18px;
		text-align-last: left;
		padding: 10px;
		line-height: 20px;
		letter-spacing: normal;
	}
	
}
