.body-page__hero-posts {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	padding: 5px 5%;
	margin: 0 10%;
	background: rgb(63, 63, 63);
	width: 80%;
}
.body-page__hero-posts-title {
	align-self: start;
}
.body-page__hero-posts-textarea {
	margin: 5px;
	min-width: 100%;
	min-height: 100%;
	height: auto;
}

.body-page__hero-posts-textarea-input {
	overflow: hidden;
	width: 100%;
	max-width: 100%;
	min-height: 50px;
	height: auto;
	font-size: 16px;
	font-weight: 500;
	padding: 5px;
	background: rgb(206, 206, 206);
	transition: all 0.2s linear ;
}
.body-page__hero-posts-submit,
.blue-btn,
.red-btn {
	align-self: flex-end;
	width: 100px;
	height: 30px;
	font-size: 18px;
	font-weight: 900;
}

.blue-btn {
	color: #03e9f4;
	background: rgb(196, 195, 195);
	border: 1.5px solid #03e9f4;
	border-radius: 7px;
}
.red-btn {
	color: #941505;
	background: rgb(196, 195, 195);
	border: 1.5px solid #03e9f4;
	border-radius: 7px;
}
.blue-btn:hover {
	background-color: #03e9f4;
	color: #00a5ad;
	-webkit-box-reflect: below 1px linear-gradient(transparent, #0005);
	transition: 0.2s;
}
.red-btn:hover {
	background-color: #b10909;
	color: #00a5ad;
	-webkit-box-reflect: below 1px linear-gradient(transparent, #0005);
	transition: 0.2s;
}
.blue-btn:active {
	box-shadow: 0px 0px 10px #03e9f4, 0px 0px 20px #03e9f4, 0px 0px 25px #03e9f4, 0px 0px 30px #03e9f4;
	-webkit-box-reflect: below 1px linear-gradient(transparent, #0005);
}
.red-btn:active {
	box-shadow: 0px 0px 10px  #f43303, 0px 0px 20px #f43303, 0px 0px 25px  #f43303, 0px 0px 30px #f43303;
	-webkit-box-reflect: below 1px linear-gradient(transparent, #0005);
}

