.footer{position:absolute;left:0;bottom:0;width:100%;height:40px;background:#2b312f;display:flex;justify-content:space-between;align-items:center;padding:0 30px 10px 30px;border-top:2px solid #555;z-index:30}.footer a{text-decoration:none;font-size:calc(10px + 2vmin);color:white}

.App {
	text-align: center;
	background-color: #282c34;
	min-height: 100vh;
	display: block;
	font-size: calc(10px + 2vmin);
	color: white;
	width: 100%;
	height: 100vh;
	overflow: hidden;
	position: relative;
}
* {
	padding: 0px;
	margin: 0px;
	border: 0px;
}
*,
*:before,
*:after {
	box-sizing: border-box;
}
:focus,
:active {
	outline: none;
}
a:focus,
a:active {
	outline: none;
}
aside,
nav,
footer,
header,
section {
	display: block;
}
html,
body {
	height: 100%;
}
body {
	line-height: 1;
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}
input::-ms-clear {
	display: none;
	padding: 5px;
}
textarea {
	padding: 10px;
	border-radius: 5px;
}
button {
	cursor: pointer;
}
button::-moz-focus-inner {
	padding: 0;
	border: 0;
}
a,
a:visited {
	text-decoration: none;
}
a:hover {
	text-decoration: none;
}
ul li {
	list-style: none;
}
img {
	vertical-align: top;
}
h1,
h2,
h3,
h4,
h5,
h6 {
	font-weight: inherit;
	font-size: inherit;
}

